import React, { useState } from 'react';
import {
  MapContainer,
  TileLayer,
  Marker,
  Popup,
  Polygon,
  useMapEvents,
} from 'react-leaflet';
import L from 'leaflet';
import * as turf from '@turf/turf'; // Import turf.js
import { TwitterPicker } from 'react-color'; // Import react-color color picker
import '../leaflet.css';

// Fix for the default marker icon not showing up
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl:
    'https://unpkg.com/leaflet@1.7.1/dist/images/marker-icon-2x.png',
  iconUrl: 'https://unpkg.com/leaflet@1.7.1/dist/images/marker-icon.png',
  shadowUrl: 'https://unpkg.com/leaflet@1.7.1/dist/images/marker-shadow.png',
});

// Create a custom marker icon using CSS-based circles
const createCustomMarkerIcon = (color) => {
  return L.divIcon({
    className: 'custom-marker',
    html: `<div style="background-color: ${color}; width: 20px; height: 20px; border-radius: 50%; border: 2px solid white;"></div>`,
    iconSize: [20, 20], // Size of the marker
    iconAnchor: [10, 10], // Position the marker properly on the point
  });
};

// Component to handle map clicks and add markers
const MapClickHandler = ({ onClick }) => {
  useMapEvents({
    click(e) {
      onClick(e.latlng); // Pass latitude and longitude on click
    },
  });
  return null;
};

const MapComponent = () => {
  const [markers, setMarkers] = useState([]); // Store clicked marker positions
  const [currentPolygon, setCurrentPolygon] = useState(null); // Store the current polygon
  const [savedPolygons, setSavedPolygons] = useState([]); // Store the previously saved polygons
  const [selectedColor, setSelectedColor] = useState('#FF0000'); // Default color for the polygon and markers
  const [showColorPicker, setShowColorPicker] = useState(false); // Control color picker visibility
  const [colorPickerPosition, setColorPickerPosition] = useState(null); // Store position of the first marker

  // Add marker on map click
  const handleMapClick = (latlng) => {
    setMarkers((prevMarkers) => [...prevMarkers, latlng]); // Append new marker to the array
    console.log(
      `Marker added at Latitude: ${latlng.lat}, Longitude: ${latlng.lng}`
    );

    if (markers.length === 0) {
      // Show the color picker when the first marker is added
      setShowColorPicker(true);
      setColorPickerPosition(latlng); // Set the position of the color picker popup
    }
  };

  // Create a polygon using the markers (with Convex Hull)
  const handleSaveLocation = () => {
    if (markers.length > 2) {  // At least 3 points are needed to form a polygon
      // Convert markers to GeoJSON points
      const points = markers.map(marker => [marker.lng, marker.lat]);  // [lng, lat] for turf.js

      // Create a GeoJSON FeatureCollection from the points
      const pointsCollection = turf.featureCollection(points.map(p => turf.point(p)));

      // Calculate the convex hull (the polygon that encompasses all points)
      const hull = turf.convex(pointsCollection);

      if (hull) {
        // Extract the coordinates of the convex hull to use for the Leaflet polygon
        const hullCoordinates = hull.geometry.coordinates[0].map(coord => ({
          lat: coord[1],
          lng: coord[0]
        }));

        // Only use the markers that form the convex hull (boundary markers)
        const boundaryMarkers = hullCoordinates.map(coord => ({
          lat: coord.lat,
          lng: coord.lng,
        }));

        // Save the polygon, its color, and its boundary markers
        setSavedPolygons((prevPolygons) => [
          ...prevPolygons,
          {
            coordinates: hullCoordinates,
            color: selectedColor,
            markers: boundaryMarkers,
          },
        ]);

        // Clear markers and reset current polygon to allow new polygon creation
        setMarkers([]);
        setCurrentPolygon(null);
        setShowColorPicker(false); // Hide color picker after saving polygon
      }
    } else {
      alert("You need at least 3 markers to create a polygon.");
    }
  };

  // Handle color change from color picker
  const handleColorChange = (color) => {
    setSelectedColor(color.hex);
    setShowColorPicker(false); // Hide the color picker after selecting a color
  };

  return (
    <div>
      <MapContainer
        center={[37.94168881076144,23.652952909469608]} //Unipi lat,lng
        zoom={18}
        style={{ height: '80vh', width: '100%' }}
      >
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />

        {/* Render all saved polygons with their selected colors */}
        {savedPolygons.map((polygon, idx) => (
          <React.Fragment key={`polygon-${idx}`}>
            {/* Render the polygon */}
            <Polygon positions={polygon.coordinates} pathOptions={{ color: polygon.color }} />
            
            {/* Render the boundary markers with the same color */}
            {polygon.markers.map((position, markerIdx) => (
              <Marker
                key={`boundary-marker-${idx}-${markerIdx}`}
                position={position}
                icon={createCustomMarkerIcon(polygon.color)} // Apply the color specific to this polygon
              >
                <Popup>
                  Boundary Marker {markerIdx + 1}: Latitude {position.lat}, Longitude {position.lng}
                </Popup>
              </Marker>
            ))}
          </React.Fragment>
        ))}

        {/* Render markers being added for the current polygon */}
        {markers.map((position, idx) => (
          <Marker
            key={idx}
            position={position}
            icon={createCustomMarkerIcon(selectedColor)} // Apply the selected color to the marker icon
          >
            <Popup>
              Marker {idx + 1}: Latitude {position.lat}, Longitude {position.lng}
            </Popup>
          </Marker>
        ))}

        {/* Render the current polygon being created */}
        {currentPolygon && (
          <Polygon positions={currentPolygon} pathOptions={{ color: selectedColor }} />
        )}

        {/* Handle map click */}
        <MapClickHandler onClick={handleMapClick} />
      </MapContainer>

      {/* Button to save location and create polygon */}
      <button onClick={handleSaveLocation} style={{ marginTop: '10px' }}>
        Save Location
      </button>

      {/* Render Color Picker inside the map UI near the first marker */}
      {showColorPicker && colorPickerPosition && (
        <div
          style={{
            position: 'fixed',
            top: '50vh',//top: `${colorPickerPosition.lat}px`, // Adjust this according to your layout
            left: '50%',//left: `${colorPickerPosition.lng}px`, // Adjust this according to your layout
            zIndex: 1000, // Make sure it appears on top of the map
            transform: 'translate(-50%, -50%)',
          }}
        >
          <TwitterPicker
            color={selectedColor}
            onChangeComplete={handleColorChange}
          />
        </div>
      )}
    </div>
  );
};

function Dogparks() {
  return (
    <div>
      {/* <p className='Shihan'>Display a map with nearby Dog Parks</p>
      <h1>My Leaflet Map</h1> */}
      <MapComponent />
    </div>
  );
}

export default Dogparks;
